@import "../../../styles/_settings.scss";

.withShadow {
  box-shadow: 0 1px 10px 1px rgb(150 150 150 / 50%);
  transition: all .3s;
}
.notShadow {
  box-shadow: 0;
  transition: all .3s;
}

.header-sticky {
  position: sticky;
  top: 0;
  background-color: $white;
  z-index: 100;
}

.header {
  z-index: 100;
  top: 0;
  transition: all .3s;
  height: 70px;
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header  .logo {
  font-weight: 700;
  font-size: 2.25rem;
  line-height: 0.9;
}

.header  .logo .logo-sub {
  font-weight: 600;
  font-size: 1.75rem;
  color: $color1;
  margin-left: 2px;
  display: inline-block;
}

.header .nav {
  display: flex;
  align-items: center;
  transition: all .3s;
}

.header .nav .link {
  margin-right: 2.5em;
  font-size: 1.25rem;
  font-weight: 700;
  position: relative;
  color: $dark;
}
.header .nav .link:nth-child(4) {
  margin-right: 0;
}
.header .nav .link::after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: $color1;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}

.header .nav .link:hover:after {
  width: 100%; 
  left: 0; 
}

.active::after {
  width: 100%!important; 
  left: 0!important; 
  height: 3.5px!important;
}

#hamburger {
  width: 40px;
  height: 25px;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
}

#hamburger span {
  display: block;
  position: absolute;
  height: 5px;
  width: 50%;
  background: $color1;
  opacity: 1;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

#hamburger span:nth-child(even) {
  left: 50%;
  border-radius: 0 5px 5px 0;
}

#hamburger span:nth-child(odd) {
  left:0px;
  border-radius: 5px 0 0 5px;
}

#hamburger span:nth-child(1), #hamburger span:nth-child(2) {
  top: 0px;
}

#hamburger span:nth-child(3), #hamburger span:nth-child(4) {
  top: 12px;
}

#hamburger span:nth-child(5), #hamburger span:nth-child(6) {
  top: 24px;
}

#hamburger.open span:nth-child(1),#hamburger.open span:nth-child(6) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

#hamburger.open span:nth-child(2),#hamburger.open span:nth-child(5) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

#hamburger.open span:nth-child(1) {
  left: 5px;
  top: 9px;
}

#hamburger.open span:nth-child(2) {
  left: calc(50% - 5px);
  top: 9px;
}

#hamburger.open span:nth-child(3) {
  left: -50%;
  opacity: 0;
}

#hamburger.open span:nth-child(4) {
  left: 100%;
  opacity: 0;
}

#hamburger.open span:nth-child(5) {
  left: 5px;
  top: 20px;
}

#hamburger.open span:nth-child(6) {
  left: calc(50% - 5px);
  top: 20px;
}

@media (min-width: 992px) {
  #hamburger {
    display: none;
  }  
}

@media (max-width: 991px) {
  .header .nav {
    display: flex;
    visibility: hidden;
    opacity: 0;
    height: 100vh;
    width: 100%;
    position: fixed;
    top: 70px;
    left: 0;
    flex-direction: column;
    padding: 2em 4em;
    background-color: $dark;
  }  

  .header-background-dark {
    background-color: $dark;
    color: $white;
    transition: all .3s;
  }

  .show-mobile {
    visibility: visible!important;
    opacity: 100!important;
    transition: all .3s;
    z-index: 200!important;
  }
  
  .header .nav .link {
    color: $white;
    margin: 1.5rem 0;
    font-size: 2rem;
  }
  
}
@media (max-width: 576px) {
  .header .nav {
    top: 50px;
  }

  .header {
    height: 50px;
    padding: 0;
  }

  .header  .logo {
    font-weight: 700;
    font-size: 1.5rem;
  }

  .header  .logo .logo-sub {
    font-size: 1.1rem;
  }

  #hamburger {
    width: 30px;
    height: 25px;
  }
  #hamburger span {
    height: 4px;
  }
  
  #hamburger span:nth-child(1), #hamburger span:nth-child(2) {
    top: 0px;
  }
  
  #hamburger span:nth-child(3), #hamburger span:nth-child(4) {
    top: 8px;
  }
  
  #hamburger span:nth-child(5), #hamburger span:nth-child(6) {
    top: 16px;
  }

  #hamburger.open span:nth-child(1) {
    left: 0;
    top: 5px;
  }
  
  #hamburger.open span:nth-child(2) {
    left: calc(50% - 5px);
    top: 5px;
  }
  
  #hamburger.open span:nth-child(3) {
    left: -50%;
    opacity: 0;
  }
  
  #hamburger.open span:nth-child(4) {
    left: 100%;
    opacity: 0;
  }
  
  #hamburger.open span:nth-child(5) {
    left: 0;
    top: 16px;
  }
  
  #hamburger.open span:nth-child(6) {
    left: calc(50% - 5px);
    top: 16px;
  }
}