.items-container {
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(400px,1fr));
  grid-column-gap: 15px;
  -moz-column-gap: 15px;
  column-gap: 15px;
  grid-row-gap: 15px;
  -moz-row-gap: 15px;
  row-gap: 15px;
  max-width: 900px;
  margin: 0 auto;
}
@media (min-width: 768px) and (max-width: 991px) {
  .items-container {
    grid-template-columns: repeat(auto-fill,minmax(300px,1fr));
    max-width: 700px;
  }
}

@media (max-width: 768px) {
  .items-container {
    display: block;
  }
}