@import '../../../styles/_settings.scss';

.section-text-title {
  font-size: 2rem;
  margin-bottom: 2rem;
  border-left: 5px solid $color1;
  padding: 0 0 0 15px;
  text-align: left;
}

.section-text {
  color: $grey;
  font-size: 1.25rem;
  padding-right: 3rem;
}

@media (max-width: 768px) {
  .section-text {
    padding-right: 0!important;
  }
}

@media (min-width: 992px) and (max-width: 1200px) {
  .section-text-title {
    font-size: 1.9rem;
    margin-bottom: 1.9rem;
    padding: 0 0 0 14px;
  }
  .section-text {
    font-size: 1.20rem;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .section-text-title {
    font-size: 1.8rem;
    margin-bottom: 1.8rem;
    padding: 0 0 0 13px;
  }
  .section-text {
    font-size: 1.15rem;
  }
}

@media (min-width: 576px) and (max-width: 767px) {
  .section-text-title {
    font-size: 1.7rem;
    margin-bottom: 1.7rem;
    padding: 0 0 0 12px;
  }
  .section-text {
    font-size: 1.10rem;
  }
}

@media (max-width: 575px) {
  .section-text-title {
    font-size: 1.6rem;
    margin-bottom: 1.6rem;
    padding: 0 0 0 11px;
  }
  .section-text {
    font-size: 1.05rem;
  }
}