@import "../../../styles/_settings.scss";

.hero {
  display: flex;
}

.hero .text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hero .text h1 {
  display: inline-block;
  font-size: 6rem;
  text-transform: uppercase;
  line-height: 1;
  letter-spacing: 3px;
  margin-bottom: 3rem;
}

.hero .text span {
  background-color: $color1;
  display: inline-block;
  margin-left: 10px;
  width: 70px;
  height: 7px;
  opacity: 0;
  animation-name: hero-border;
  animation-duration: 1.75s;
  animation-delay: 1s;
  animation-iteration-count: infinite;
}

.hero .text p {
  font-size: 1.5rem;
  color: $grey;
  line-height: 1.3;
  font-weight: 400;
  margin: 0;
}
.hero .image {
  margin-left: auto;
}

@keyframes hero-border {
  0% {opacity: 0;}
  50% {opacity: 1;}
  100% {opacity: 0;}
}

@media (min-width: 992px) and (max-width: 1200px) {
  .hero .text h1 {
    font-size: 4rem;
  }

  .hero .text p {
    font-size: 1.5rem;
    margin: 0;
  }

  .hero .text span {
    width: 50px;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .hero .text h1 {
    font-size: 3rem;
  }

  .hero .text p {
    font-size: 1rem;
    margin: 0;
  }

  .hero .text span {
    width: 50px;
  }
}

@media (min-width: 576px) and (max-width: 767px) {
  .hero .text h1 {
    font-size: 3rem;
  }

  .hero .text p {
    font-size: 1rem;
    margin: 0 0 4rem 0;
    text-align: right;
  }

  .hero .text span {
    width: 50px;
  }
}

@media (min-width: 400px) and (max-width: 576px) {
  .hero .text h1 {
    font-size: 2.5rem;
  }

  .hero .text p {
    font-size: 1rem;
    margin: 0 0 4rem 0;
    text-align: right;
  }

  .hero .text span {
    width: 30px;
  }
}

@media (min-width: 320px) and (max-width: 400px) {
  .hero .text h1 {
    font-size: 2.2rem;
  }

  .hero .text p {
    white-space: wrap;
    font-size: 1rem;
    margin: 0 0 4rem 0;
    text-align: right;
  }

  .hero .text span {
    width: 20px;
  }
}

@media (max-width: 320px) {
  .hero .text h1 {
    font-size: 1.6rem;
    margin: 0 0 2rem 0;
  }

  .hero .text p {
    white-space: wrap;
    font-size: 1rem;
    margin: 0 0 3rem 0;
    text-align: right;
  }

  .hero .text span {
    width: 20px;
  }
}