@import "../../../styles/_settings.scss";

.item-container {
  border-radius: 10px;
  margin: 1rem;
  box-shadow: 0 3px 20px rgb(0 0 0 / 20%);
  transition: transform .2s ease;
  color: black;
  z-index: 2;
  max-width: 700px;
}

.item-container:hover {
  transform: scale(1.01);
  box-shadow: 0 4px 20px 3px rgb(0 0 0 / 20%);
  z-index: 2;
}

.item-text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0.5rem 1rem 1rem 1rem;
  line-height: 1;
}

.item-text h2 a {
  color: $color1;
}

.item-text h2 {
  font-size: 1.5rem;
  line-height: 1;
  padding: 0 0 1rem 0;
}

.item-text h2 a:hover {
  text-decoration: underline!important;
}

.item-text p {
  margin: 0;
  font-size: 1.1rem;
  color: $grey;
  padding: 0 0 2rem 0;
}

.item-image {
  width: 22px;
  height: 22px;
}

.item-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0 1rem 0;
}

// spaces between cards
@media (max-width: 768px) {
  .item-container {
    margin: 0 0 5rem 0;
  }
  .item-container:nth-child(6) {
    margin-bottom: 0;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .item-image {
    width: 22px;
    height: 22px;
  }
  
  .item-footer {
    flex-direction: column;
  }

  .item-footer span {
    padding: 1rem 0 0 0;
  }
}

@media (min-width: 576px) and (max-width: 768px) {
  
  .item-image {
    width: 30px;
    height: 30px;
  }
  
  .item-footer {
    flex-direction: column;
  }
  .item-footer span {
    padding: 1rem 0 0 0;
  }
}

@media (min-width: 400px) and (max-width: 576px) {
  .item-text h2 {
    font-size: 1.3rem;
    padding: 0 0 0.5rem 0;
  }
  
  .item-text p {
    font-size: 1rem;
    padding: 0 0 1.5rem 0;
  }
  
  .item-image {
    width: 25px;
    height: 25px;
  }
  
  .item-footer {
    padding: 0 0 1rem 0;
    flex-direction: column;
  }

  .item-footer span {
    padding: 1.5rem 0 0 0;
  }
}


@media (max-width: 400px) {
  .item-text h2 {
    font-size: 1.1rem;
    padding: 0 0 0.5rem 0;
  }
  
  .item-text p {
    font-size: 0.9rem;
    padding: 0 0 1.5rem 0;
  }
  
  .item-image {
    width: 30px;
    height: 30px;
  }
  
  .item-footer {
    padding: 0 0 1rem 0;
    flex-direction: column;
  }

  .item-footer span {
    padding: 1.5rem 0 0 0;
  }
}