@import "./_settings.scss";
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap');

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.7;
  font-size: 16px;
  font-family: 'Noto Sans', sans-serif;
  background-color: $white;
}

a, a:hover, a:visited {
  text-decoration: none!important;
  cursor: pointer;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    list-style: none;
  }
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.d-flex-col-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media (max-width: 768px) {
  .column-reverse {
    flex-direction: column-reverse;
  }
  .p-l-section {
    padding-left: 0!important;
  }
}

.p-y-section {
  padding: 5rem 0;
}

.p-y-sm-section {
  padding: 4rem 0;
}

.p-b-sm-section {
  padding: 0 0 4rem 0;
}

.p-l-section {
  padding-left: 4rem;
}

@media (min-width: 768px) and (max-width: 991px) {
  .p-y-section {
    padding: 4rem 0!important;
  }
  
  .p-y-sm-section {
    padding: 3rem 0!important;
  }
  
  .p-b-sm-section {
    padding: 0 0 3rem 0!important;
  }
}

@media (min-width: 576px) and (max-width: 767px) {
  .p-y-section {
    padding: 3rem 0!important;
  }
  
  .p-y-sm-section {
    padding: 2rem 0!important;
  }
  
  .p-b-sm-section {
    padding: 0 0 2rem 0!important;
  }
}

@media (max-width: 575px) {
  .p-y-section {
    padding: 2rem 0!important;
  }
  
  .p-y-sm-section {
    padding: 1.5rem 0!important;
  }
  
  .p-b-sm-section {
    padding: 0 0 1.5rem 0!important;
  }
}

@keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.slide-bottom {
  -webkit-animation: slide-bottom 0.3s both;
	animation: slide-bottom 0.3s both;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fade-in {
	-webkit-animation: fade-in 0.8s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	animation: fade-in 0.8s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes slide-top {
  0% {
    opacity: 0;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slide-top {
  0% {
    opacity: 0;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

.slide-top {
	-webkit-animation: slide-top 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	animation: slide-top 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}