@import '../../../../styles/_settings.scss';

form {
  text-align: left;

  a:focus {
    outline: none!important;
  }

  a:hover {
    color: $color1;
  }

  .form-input {
    position: relative;
    margin: 15px 0;
  }

  label {
    width: 100%;
    position: absolute;
    left: 0;
    top: -50px;
    transition: all .3s ease;
    margin-top: 20px;
  }

  input[type=email], textarea {
    width: 300px;
    font-family: 'Roboto', sans-serif;
    display: block;
    outline: none;
    border: none;
    padding: 10px 10px 10px 5px;
    border-bottom: 2px solid $dark;
    background: inherit;
    margin: 3rem 0;
    letter-spacing: .1em;
    font-size: 1.1rem;
  }
  
  textarea {
    height: 100px;
    resize: none;
  }

  input[type=email]:focus, textarea:focus {
    border-bottom: 2px solid $color1;
  }

  input[type=email]:focus ~ label,
  textarea:focus ~ label 
  {
    top: -60px;
    font-size: 0.8rem;
    color: $color1;
  }

  button {
    margin-top: 20px;
    outline: none !important;
    border: 2px solid transparent;
    border-radius: 10px;
    font-size: 1.4rem;
    color: $white;
    border-radius: 30px;
    padding: 0.3rem 2.5rem;
    background-color: $color1;
    font-weight: bold;
  }
  
  button:hover {
    cursor: pointer;
    color: $color1;
    background-color: $white;
    border: 2px solid $color1;
  }
}

@media (max-width: 768px) {
  form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 5rem;

    input[type=email], textarea {
      margin: 1rem 0;
    }

    label {
      top: -20px;
    }

    input[type=email]:focus ~ label,
    textarea:focus ~ label 
    {
      top: -30px;
      font-size: 0.8rem;
      color: $color1;
    }

    button {
      margin-top: 10px;
      font-size: 1.2rem;
    }
  }
}
@media (max-width: 350px) {
  input[type=email], textarea {
    width: 200px!important;
  }
}