@import "../../../../styles/_settings.scss";

.skill {
  display: inline-block;
  padding: 0.5rem;
  border-radius: 5px;
  transition: all .3s;
}

.skill:hover {
  transform: translateY(-3px);
}
.skill .title {
  color: black;
}
.skill .image img {
  width: 70px;
  height: 70px;
}

@media (min-width: 576px) and (max-width: 1200px) {
  .skill {
    padding: 0;
    font-size: 0.9rem;
  }
  .skill .image img {
    width: 50px;
    height: 50px;
  }
}

@media (max-width: 576px) {
  .skill {
    padding: 0;
    font-size: 0.9rem;
  }
  .skill .image img {
    width: 40px;
    height: 40px;
  }
}