.skills-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(85px,1fr));
  grid-column-gap: 10px;
  -moz-column-gap: 10px;
  column-gap: 10px;
  grid-row-gap: 10px;
  row-gap: 10px;
  white-space: nowrap;
}

@media (min-width: 576px) and (max-width: 1200px) {
  .skills-wrapper {
    grid-template-columns: repeat(auto-fill,minmax(70px,1fr));
    grid-column-gap: 5px;
    -moz-column-gap: 5px;
    column-gap: 5px;
    grid-row-gap: 5px;
    row-gap: 5px;
  }
}

@media (max-width: 576px) {
  .skills-wrapper {
    grid-template-columns: repeat(auto-fill,minmax(90px,1fr));
    grid-column-gap: 2px;
    -moz-column-gap: 2px;
    column-gap: 2px;
    grid-row-gap: 2px;
    row-gap: 2px;
  }
}

